	module.exports = {"data":{"markdownRemark":{"html":"<p>Beaucoup pensent aujourd'hui que le parler en langue est <strong>un don indispensable</strong> pour communiquer avec Dieu de manière personnelle et secrète, ou pour rendre <em>\"d'excellentes actions de grâces\" (1 Corinthiens 14:17)</em>. Ce n'est cependant pas ce que la Parole de Dieu nous enseigne.</p>\n<p>Selon l'apôtre Paul, nous pouvons prier secrètement et discrètement très simplement, en parlant à Dieu dans notre esprit partout où nous sommes (au travail, dans la rue, etc...) car Dieu nous entend :</p>\n<blockquote>\n<p>« Faites en tout temps par l'Esprit toutes sortes de prières et de supplications. » <br> <small>- Ephésiens 6:18</small></p>\n</blockquote>\n<p>Voici également, la définition de Jésus de la prière secrète (lorsque l'on prie à voix haute) :</p>\n<blockquote>\n<p>« Lorsque vous priez, ne soyez pas comme les hypocrites, qui aiment à prier debout dans les synagogues et aux coins des rues, pour être vus des hommes. Je vous le dis en vérité, ils reçoivent leur récompense. <u>Mais quand tu pries, entre dans ta chambre, ferme ta porte, et prie ton Père qui est là dans le lieu <strong>secret</strong>;</u> <strong>et ton Père, qui voit dans le secret,</strong> te le rendra. » <br> <small>- Matthieu 6:5, 6</small></p>\n</blockquote>\n<p>Pas besoin donc de beaucoup de choses pour avoir une communication intime, personnelle et secrète avec Dieu. <strong>Il est tout près de nous</strong> et désire ardemment nous entendre, <strong>même dans l'intimité de notre chambre.</strong></p>\n<p>En ce qui concerne les actions de grâce, nul part l'apôtre Paul ne déclare qu'il est <strong>indispensable de parler en langue</strong> pour rendre d'excellentes actions de grâces. Le livre des Psaumes en est un excellent exemple d'ailleurs. Il est remplis de chants dont les paroles représentent parfois des louanges, des actions de grâces et de remerciements (Psaume 138, etc...), des prières de repentances, de confessions, ou des appels à la délivrance; et tout cela <strong>en langue hébraïque !</strong> Ces cantiques et ces psaumes étaient chantés par les Israélites dans des occasions diverses, comme lors de la pâque par exemple (Marc 14:26).</p>\n<p>Dieu nous comprends donc parfaitement quelque soit notre langue, et ce qui fait l'excellence de nos actions de grâce ou de nos prières pour Lui, c'est <strong>la disposition du coeur dont elles proviennent.</strong></p>\n<p>Paul écrivit aux Éphésiens en disant :</p>\n<blockquote>\n<p>« [...] Soyez, au contraire, <strong>remplis de l'Esprit;</strong> entretenez-vous par des <strong>psaumes</strong>, par des <strong>hymnes</strong>, et par des <strong>cantiques spirituels, chantant et célébrant <u>de tout votre coeur</u> les louanges du Seigneur;</strong> » <br> <small>- Éphésiens 5:18, 19</small></p>\n</blockquote>","frontmatter":{"title":"Mais alors, comment parler secrètement à Dieu et lui rendre d'excellentes actions de grâce ?"}}},"pathContext":{"slug":"/mais-alors-comment-parler-secretement-a-dieu-et-lui-rendre-d-excellentes-actions-de-grace/"}}

